import { MaskOperation, StencilType, gl } from '@antv/l7-core';
// 
export function getStencil(mask, maskInside) {
  return {
    enable: mask,
    mask: 0xff,
    func: {
      cmp: gl.EQUAL,
      // gl.EQUAL,
      ref: maskInside ? 1 : 0,
      mask: 1
    }
  };
}
// 
export function getStencilMask(option) {
  if (option.maskOperation === MaskOperation.OR) {
    return {
      enable: true,
      mask: 0xff,
      func: {
        cmp: gl.ALWAYS,
        ref: 1,
        mask: 0xff
      },
      opFront: {
        fail: gl.KEEP,
        zfail: gl.REPLACE,
        zpass: gl.REPLACE
      }
    };
  }
  return {
    enable: true,
    mask: 0xff,
    func: {
      cmp: option.stencilType === StencilType.SINGLE // 
      ? gl.ALWAYS : option.stencilIndex === 0 // 
      ? gl.ALWAYS : gl.LESS,
      ref: option.stencilType === StencilType.SINGLE ? 1 : option.stencilIndex === 0 ? 2 : 1,
      mask: 0xff
    },
    opFront: {
      fail: gl.KEEP,
      zfail: gl.REPLACE,
      zpass: gl.REPLACE
    }
  };
}