/**
 *  Layer  indices  attribute 
 */
export default class UpdateStyleAttributePlugin {
  apply(layer, {
    styleAttributeService
  }) {
    layer.hooks.init.tapPromise('UpdateStyleAttributePlugin', () => {
      this.initStyleAttribute(layer, {
        styleAttributeService
      });
    });
    layer.hooks.beforeRender.tap('UpdateStyleAttributePlugin', () => {
      if (layer.layerModelNeedUpdate) {
        return;
      }
      if (layer.inited) {
        this.updateStyleAttribute(layer, {
          styleAttributeService
        });
      }
    });
  }
  updateStyleAttribute(layer, {
    styleAttributeService
  }) {
    const attributes = styleAttributeService.getLayerStyleAttributes() || [];
    const filter = styleAttributeService.getLayerStyleAttribute('filter');
    if (filter && filter.needRegenerateVertices) {
      layer.layerModelNeedUpdate = true;
      attributes.forEach(attr => attr.needRegenerateVertices = false);
      return;
    }
    attributes.filter(attribute => attribute.needRegenerateVertices).forEach(attribute => {
      // / feature(s) featureIdx d
      styleAttributeService.updateAttributeByFeatureRange(attribute.name, layer.getEncodedData(),
      //  mapping 
      attribute.featureRange.startIndex, attribute.featureRange.endIndex, layer);
      attribute.needRegenerateVertices = false;
    });
  }
  initStyleAttribute(layer, {
    styleAttributeService
  }) {
    const attributes = styleAttributeService.getLayerStyleAttributes() || [];
    attributes.filter(attribute => attribute.needRegenerateVertices).forEach(attribute => {
      // / feature(s) featureIdx d
      styleAttributeService.updateAttributeByFeatureRange(attribute.name, layer.getEncodedData(),
      //  mapping 
      attribute.featureRange.startIndex, attribute.featureRange.endIndex);
      attribute.needRegenerateVertices = false;
    });
  }
}